"use client";

import React, { useEffect, useState } from "react";
import { useRouter, useParams } from "next/navigation";
import { useCart } from "../../../components/CartContext"
import { CartProvider } from "../../../components/CartContext";
import Image from "next/image";
import { Product } from "@prisma/client";

const ProductDetail: React.FC = () => {
  const router = useRouter();
  const { cart, removeFromCart, clearCart } = useCart();
  const params = useParams();
  const wsCode = params.wsCode; 
  const { addToCart } = useCart();
  const [product, setProduct] = useState<Product | null>(null);
  const [quantity, setQuantity] = useState(1);

  useEffect(() => {
    if (wsCode) {
      // Fetch product details by wsCode
      const fetchProduct = async () => {
        try {
          const response = await fetch(`/api/products/${wsCode}`);
          const data = await response.json();
          console.log(data);
          setProduct(data.product); // Assume the API returns the product object
        } catch (error) {
          console.error('Error fetching product details:', error);
        }
      };
      fetchProduct();
    }
  }, [wsCode]);

  if (!product) {
    return <div>Loading...</div>;
  }
  const handleAddToCart = () => {
    addToCart({
      productId: product.wsCode.toString(),
      name: product.name,
      price: product.salesPrice,
      quantity: 1,
      image: product.images[0] || "/default-image.jpg",
    });
    console.log(cart);
    alert("Product added to cart!");
  };

  return (

    <div className="p-6">
      <button onClick={() => router.push("/viewproducts")} className="mb-4 text-blue-500">
        Back
      </button>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div>
          {/* Images */}
        </div>
        <div>
          <h1>{product.name}</h1>
          <p>${product.salesPrice}</p>
          <div>
            <label>Quantity:</label>
            <input
              type="number"
              min="1"
              value={quantity}
              onChange={(e) => setQuantity(parseInt(e.target.value))}
            />
          </div>
          <button onClick={handleAddToCart} className="mt-4 px-4 py-2 bg-blue-500 text-white">
            Add to Cart
          </button>
        </div>
      </div>
    </div>
  );
};

export default ProductDetail;
